

import {Layer, Sprite, Rect, Stage, Group} from 'react-konva';
import React from "react"
import ReactDOM from "react-dom"
import {Player} from "./components/player.js"
import {Dungeon} from "./components/dungeon.js"



class App extends React.Component {
  constructor(props) {
    super(props);
    let rooms = 6
    let dungeonArray = []
    do {
       dungeonArray = []
       dungeonArray = generateRooms(rooms,1600 ,1600)
    } while (corrupted === true)
    let enemyArray = genEnemyArray (dungeonArray)

    this.state = {
          rooms : room,
          dungeonArray : dungeonArray,                   // x , y , width, height
          playerGraphics : jake,
          enemyGraphics : cinn,
          playerPosition : {
            name : "player",
            locationX : dungeonArray[0].x + dungeonArray[0].width /2,
            locationY : dungeonArray[0].y + dungeonArray[0].height /2,
            direction : "walkUp"
          },
          viewport : {
            width : window.innerWidth <= 1200 ? window.innerWidth -40 : 1200,
            height : window.innerHeight <= 1200 ? window.innerHeight -200: 1000,
          },
          enemyArray : enemyArray,
          lastUpdate : JSON.stringify(new Date().getTime())
    }
  }

  shouldComponentUpdate(nextProps, nextState) {  //Math.round(
  const differentX = Math.round(this.state.playerPosition.locationX) !== Math.round(nextState.playerPosition.locationX);
  const differentY = Math.round(this.state.playerPosition.locationY) !== Math.round(nextState.playerPosition.locationY);
  const differentDirection = this.state.playerPosition.direction !== nextState.playerPosition.direction;
  const arrayDiff = this.state.enemyArray !== nextState.enemyArray

  return differentX || differentY || differentDirection || arrayDiff;
};

  componentDidMount = () => {  //update position  50 times a second
      this.interval = setInterval(this.updateAllPosition, 20);
  };

  componentWillUnmount = () => {
    clearInterval(this.interval);
  };

  componentWillMount = () => {  //if its mounted listen for key presses
      this.setState({
     viewport : {
      LViewport : ((this.state.playerPosition.locationX) - (this.state.viewport.width/2) -40),
      RViewport : ((this.state.playerPosition.locationX) + (this.state.viewport.width/2) + 40),
      TViewport : ((this.state.playerPosition.locationY) - (this.state.viewport.height/2) -40),
      BViewport : ((this.state.playerPosition.locationY) + (this.state.viewport.height/2) + 40),
      width : this.state.viewport.width,
      height : this.state.viewport.height
    }
    })


    this.setState({


    })

    document.addEventListener("keydown", this.handleKeyPress)
  };

  updateAllPosition = () => {  //update position --- needs speeding up
    let currentTime = new Date().getTime();
    let updatedPosition = updatePosition(this.state.playerPosition, this.state.playerGraphics,this.state.lastUpdate, currentTime)
    if (updatedPosition !== this.state.playerPosition ){
      let wrapper = document.getElementById("wrapper")  //update scroll point
      wrapper.scrollTop = updatedPosition.locationY - this.state.viewport.height /2;
      wrapper.scrollLeft = updatedPosition.locationX - this.state.viewport.width / 2;

    }
    //update enemy
    let updateEnemy = []
    let enemyArray = this.state.enemyArray
    for (let i = 0; i < enemyArray.length; i++){
          let updatedPosition = updatePosition(enemyArray[i], this.state.enemyGraphics,this.state.lastUpdate, currentTime)
          updatedPosition.direction = randomDirection(enemyArray[i].direction)
          updateEnemy.push(updatedPosition)
        };
      this.setState({
        enemyArray : updateEnemy,
        lastUpdate : currentTime,
        playerPosition : updatedPosition,
        viewport : {
          LViewport : ((this.state.playerPosition.locationX) - (this.state.viewport.width/2) -40),
          RViewport : ((this.state.playerPosition.locationX) + (this.state.viewport.width/2) + 40),
          TViewport : ((this.state.playerPosition.locationY) - (this.state.viewport.height/2) -40),
          BViewport : ((this.state.playerPosition.locationY) + (this.state.viewport.height/2) + 40),
         width : this.state.viewport.width,
         height : this.state.viewport.height
       }
      })


  };

  handleKeyPress = (event) => {  //handle direction
    let playerPosition = this.state.playerPosition
    let newDirection;
      if (event.key == "ArrowLeft" ) {
        console.log("walking Left")
        newDirection = "walkLeft"
      } else if  (event.key == "ArrowRight") {
        console.log("you have right")
                  newDirection = "walkRight"
      //  this.setState({direction : "walkRight"})
      } else if  (event.key == "ArrowUp") {
                  newDirection = "walkUp"
        console.log("you have up")
      } else if  (event.key == "ArrowDown" && playerPosition.direction != "walkDown") {
        console.log("you have down")
                  newDirection = "walkDown"
      } else {
        console.log("not pressed direction key, assume old key ")
        newDirection = playerPosition.direction
      }
       this.setState({
         playerPosition : {
              name : this.state.playerPosition.name,
              locationX : this.state.playerPosition.locationX,
              locationY : this.state.playerPosition.locationY,
              direction : newDirection
         }
       })
  };

  buttonPress = (direction) => {
    this.setState({
      playerPosition : {
           name : this.state.playerPosition.name,
           locationX : this.state.playerPosition.locationX,
           locationY : this.state.playerPosition.locationY,
           direction : direction
      }
    })
  };


  render() {
    let enemyArray = this.state.enemyArray
    let wrapperStyle ={ width : this.state.viewport.width + "px",
                        height: this.state.viewport.height + "px",
                        overflow : "hidden"
                        }

  // let img = document.createElement('img'); // use DOM HTMLImageElement
//      img.src = 'sprite/lava.jpg';
  let enemyDisplay = enemyArray.map((enemy , key) => {

         return(
           <Player key={"Enemy" + key}
               playerGraphics={this.state.enemyGraphics}
               positionX ={enemy.locationX}
               positionY={enemy.locationY}
               direction={enemy.direction}
               viewport={this.state.viewport}
           />

         )});
    return (
      <div>
      <h2> DUNGEON CRAWLER </h2>
       <div id={"wrapper"} className={"wrapper"} style={wrapperStyle}  >
      <Stage width={1800} height={1800} >
        <Layer hitGraphEnabled={false} >
          <Rect x={0} y={0} width={1800} height={1800} fill={"black"} hitGraphEnabled={false} />
          <Dungeon dungeonArray={this.state.dungeonArray} />
          <Player
                  playerGraphics={this.state.playerGraphics}
                  positionX={Math.round(this.state.playerPosition.locationX)}
                  positionY={Math.round(this.state.playerPosition.locationY)}
                  direction={this.state.playerPosition.direction}
                  viewport={this.state.viewport}
          />
        {enemyDisplay}
        </Layer>
      </Stage>
      </div>
    </div>


  )}

};

ReactDOM.render(
  <App />,
  document.getElementById('app')
);



