import {Layer, Sprite, Stage, Group} from 'react-konva';
import React from "react"
import ReactDOM from "react-dom"

class Player extends React.Component {
    constructor(props){
      super(props);
    };

    shouldComponentUpdate(nextProps) {  //Math.round(
    let update = false; // only going to update if an enemy is in viewport and has moved / not bothered about direction
    let viewport = this.props.viewport

            if (viewport.LViewport < this.props.positionX && this.props.positionX < viewport.RViewport) {
              console.log("within width")
              if (viewport.TViewport < this.props.positionY && this.props.positionY < viewport.BViewport) {
                console.log("within height")
                if (Math.round(this.props.locationX) !== Math.round(nextProps.locationX) ||
                    Math.round(this.props.locationY) !== Math.round(nextProps.locationY) ||
                    this.props.direction !== nextProps.direction) {
                  update = true
                }
              }
            }
            console.log(viewport)

    return  update
};

    componentWillMount = () => {  //if its mounted listen for key presses

      console.log("jake is ", this.props.positionX, this.props.positionY)

    };

    render(){
      let playerGraphics = this.props.playerGraphics
      let img = document.createElement('img'); // use DOM HTMLImageElement
          img.src = playerGraphics.src;
        return (
          <Sprite image={img} x={this.props.positionX} y={this.props.positionY}
             animations={this.props.playerGraphics.animation}
             animation={this.props.direction}
             frameRate={playerGraphics.frameRate}
             ref={(node => {if(node && !node.isRunning()) node.start()})}  //to start animation
             hitGraphEnabled={false}
             />

      )}
};



module.exports = {
    Player: Player


}



